import WomenImg from "../assets/img/banner-woman2.webp";
import Logo from "../assets/img/kirtiImg.png"

function Hero() {
  return (
    <section
      id="home"
      className="lg:h-[85vh] flex items-center bg-primary lg:bg-cover lg:bg-center lg:big-no-repeat py-32 lg:py-0 overflow-hidden"
    >
      <div className="container mx-auto h-full">
        <div className="flex items-center h-full pt-8">
          {/* left side  */}
          <div className="flex-1 flex flex-col items-center lg:items-start">
            <p className="text-3xl  text-accent mb-[5px]">
              Hey , I'm Kirti Gupta
            </p>
            <h1 className="text-4xl leading-[44px] md:text-5xl  lg:text-7xl lg:leading-[1.2]">
              I am a Full-Stack Web Developer.
            </h1>
            <a href="https://drive.google.com/file/d/1s199NvK7HOF8Bw74ONLkfWmN5kRExm2w/view?usp=sharing" target="_blank">
              <button className="btn btn-md mt-[18px] bg-accent hover:bg-secondary-hover md:btn-lg transition-all">
                Resume
              </button>
            </a>
          </div>
          {/* right side  */}
          {/* Image  */}
          <div className="hidden lg:flex flex-1 justify-end items-end h-full">
            <img
              width={350}
              src={Logo}
              alt=""
            />
          </div>
        </div>
      </div>
    </section>
  );
}

export default Hero;
